@media only screen and (min-width: 1551px) { 


.potionsView {
    width: 100%;
    height: 100vh;
}

.potionView_middle {
    width: 100%;
    height: 80%;
    background-image: url("../../image_assets/general/background_image.webp");
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center; 
}

.potionView_middle_container {
    width: 1024px;
    height: 100%;
    background: rgb(190,146,116);
    background: radial-gradient(circle, rgba(190,146,116,1) 0%, rgba(109,89,76,1) 100%);
    z-index: 100;
    overflow: scroll;
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none; /* for Firefox */
    overflow-y: scroll; 
}

.potionView_middle_container::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}

.potionView_middle_level {
    width: 100%;
    height: 175px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 5px;
}

.potionView_middle_level_title {
    font-size: 35px;
    color: white;
    margin: 0;
    font-family: 'Luckiest Guy', cursive;
}


.potionView_middle_level_progressbar {
    width: 70%;
    height: 30px;
    background-color: #4d4022;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.potionView_middle_level_progressbar_limiter {
    width: 98.7%;
    height: 74%;
}

.progressbar_fill {width: 0.5%; height: 100%;}


.potionView_middle_level_btn {
    width: 200px;
    cursor: pointer;
    margin-top: 5px;
}


.potionView_middle_cards {
    width: 100%;
    height: calc(100% - 150px);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 3%; 
    margin-top: 10px;
}

.cardBox {
    width: 20%;
    position: relative;
    text-align: center;
}

.cards {
    width: 100%;
}



.cards_amount_text {
    width: 20px;
    position: absolute;
    top: 85%;
    left: 9.2%;
    color: white;
    font-family: 'Luckiest Guy', cursive;
    font-size: 15px;
    margin: 0;
}

.cards_no_amount {opacity: 0.5;}
.cards_clickable {cursor: pointer;}
.cards_not_discovered {opacity: 0.5; }



.extra_space {
    height: 60px;
    width: 100%;
}



.potionView_footer {
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    background-color: #2e1e12;
    position: relative;
   
}

.potionView_footer_container {
    width: 500px;
    height: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    gap: 5px;
    overflow: hidden;
  
}
.bottom_recipe_container {
    height: 100%;
    width: auto;
    position: relative;
}
.footer_ingred_amount {
    font-size: 10px;
    width: 20px;
    margin: 0;
    color: white;
    font-family: 'Luckiest Guy', cursive;
    position: absolute;
    top: 70.5%;
    left: 66%;
    text-align: center;
}

.card_ingredient {height: 100%;}
.chosen_bottle {height: 90%;}

.potionView_footer_actions {
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    gap: 15px;
    position: absolute;
    top: -60px;
    z-index: 100;
  
}

#potionView_actions {
    display: none;
}

.potion_action_btns {
    width: 120px;
    height: 35px;
    font-size: 20px;
    color: white;
    font-family: 'Luckiest Guy', cursive;
    border-radius: 5px;
    background: #2e1e12;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    cursor: pointer;
}
.potion_action_btns:hover {
    background: #e8c242;
}





}



