@media only screen and (max-width: 1000px) { 



    .potionsView {
        width: 100%;
        height: 100vh;
    }
    
    .potionView_middle {
        width: 100%;
        height: 80%;
        background-image: url("");
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        justify-content: center; 
    }
    
    .potionView_middle_container {
        width: 1024px;
        height: 100%;
        background: rgb(190,146,116);
        background: radial-gradient(circle, rgba(190,146,116,1) 0%, rgba(109,89,76,1) 100%);
        z-index: 100;
        overflow: scroll;
        -ms-overflow-style: none; /* for Internet Explorer, Edge */
        scrollbar-width: none; /* for Firefox */
        overflow-y: scroll; 
    }
    
    .potionView_middle_container::-webkit-scrollbar {
        display: none; /* for Chrome, Safari, and Opera */
    }
    
    .potionView_middle_level {
        width: 100%;
        height: 175px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        row-gap: 5px;
    }
    
    .potionView_middle_level_title {
        font-size: 35px;
        color: white;
        margin: 0;
        font-family: 'Luckiest Guy', cursive;
    }
    
    
    .potionView_middle_level_progressbar {
        width: 70%;
        height: 30px;
        background-color: #4d4022;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .potionView_middle_level_progressbar_limiter {
        width: 98.7%;
        height: 74%;
    }
    
    .progressbar_fill {width: 0.5%; height: 100%;}
    
    
    .potionView_middle_level_btn {
        width: 175px;
        cursor: pointer;
        margin-top: 5px;
        padding-top: 4px;
    }
    
    
    .potionView_middle_cards {
        width: 100%;
        height: calc(100% - 150px);
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        gap: 2%; 
        margin-top: -10px;
    }
    
    .cardBox {
        width: 20%;
        position: relative;
        text-align: center;
        margin-left: 10px;
        margin-top: 10px;
    }
    
    .cards {
        width: 100%;
    }
    
    
    
    .cards_amount_text {
        width: 20px;
        position: absolute;
        top: 85.5%;
        left: 8.2%;
        color: white;
        font-family: 'Luckiest Guy', cursive;
        font-size: 15px;
        margin: 0;
    }
    
    .cards_no_amount {opacity: 0.5;}
    .cards_clickable {cursor: pointer;}
    .cards_not_discovered {opacity: 0.5; }
    
    
    
    .extra_space {
        height: 60px;
        width: 100%;
    }
    
    
    
    .potionView_footer {
        width: 100%;
        height: 10%;
        display: flex;
        justify-content: center;
        background-color: #2e1e12;
        position: relative;
       
    }
    
    .potionView_footer_container {
        width: 500px;
        height: 100%;
        display: none;
        justify-content: center;
        align-items: center;
        gap: 5px;
      
    }
    .bottom_recipe_container {
        justify-content: center;
        height: 100%;
        width: auto;
        display: flex;
        position: relative;
        align-items: center;
    }
    .footer_ingred_amount {
        font-size: 10px;
        width: 20px;
        margin: 0;
        color: white;
        font-family: 'Luckiest Guy', cursive;
        position: absolute;
        top: 65.5%;
        left: 64%;
        text-align: center;
    }
    
    .card_ingredient {height: 85%;}
    .chosen_bottle {height: 80%;}
    
    .potionView_footer_actions {
        height: 60px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        gap: 15px;
        position: absolute;
        top: -60px;
        z-index: 100;
      
    }
    
    #potionView_actions {
        display: none;
    }
    
    .potion_action_btns {
        width: 120px;
        height: 35px;
        font-size: 20px;
        color: white;
        font-family: 'Luckiest Guy', cursive;
        border-radius: 5px;
        background: #2e1e12;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 10px;
        margin-right: 10px;
        cursor: pointer;
    }
    .potion_action_btns:hover {
        background: #e8c242;
    }
    .potion_action_btns>p {
        transform: translateY(3px);
    }
    




    /*  POTION INFO    */


    

    .potionInfo_container {
        z-index: 500;
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.7);
        justify-content: center;
        align-items: center;
    }
    
    #potionInfo {
        display: none;
    }
    
    .potionInfo_scroll {
      width: 400px;
      height: 565px;
      background-image: url("../../../image_assets/general/scroll.webp");
      background-repeat: no-repeat;
      background-size: contain;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 5px;
    }
    .potionInfo_scroll_top {
      width: 100%; 
      height: 90px; 
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
    }
    .potionInfo_scroll_top>img {height: 35px; margin-right: 30px; cursor: pointer;}
    .potionInfo_scroll_title {
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;  
      flex-direction: column;
      font-size: 30px;
      font-family: 'Luckiest Guy', cursive;
    }
    .potionInfo_scroll_description {
      width: 84%;
      height: auto;
      text-align: center;
      font-family: 'Permanent Marker', cursive;
      font-size: 19px;
    }
    .potionInfo_scroll_flask {
      width: 85%;
      height: 120px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .potionInfo_scroll_flask>img {height: 100px;}
    .potionInfo_scroll_effect {
      width: 85%;
      height: 145px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
    }
    #effectTitle {
      font-family: 'Luckiest Guy', cursive;
      font-size: 20px;
      margin-bottom: 5px;
    }
    .potionInfo_scroll_effect>p {
      margin: 0;
      font-family: 'Permanent Marker', cursive;
      font-size: 18px;
    }





    /* POTIONSELECTPLAYERS */

    .potionSelectPlayers_centerSection_angel {
        width: 700px;
        height: 490px;
        position: relative;
        background-image: url("../../../image_assets/general/select_player_bless.webp");
        background-size: cover;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
    }
    .potionSelectPlayers_centerSection_demon {
        width: 700px;
        height: 490px;
        position: relative;
        background-image: url("../../../image_assets/general/select_player_curse.webp");
        background-size: cover;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
    }




}



 /* iPad 9-inch */

/* Exact metric for iPad 9"-inch portrait Safari brower (no extra files) */
@media only screen and (max-width: 768px) and (min-width: 768px) and (max-height: 954px) and (min-height: 954px)  { .potionsView {overflow: hidden; height: 97vh;}}

/* Exact metric for iPad 9"-inch portrait Safari brower (with extra files) */
@media only screen and (max-width: 768px) and (min-width: 768px) and (max-height: 921px) and (min-height: 921px)  { .potionsView {overflow: hidden; height: 93vh;}}

/* iPad 10.2-inch - Safari */

/* Exact metric for iPad 10.2"-inch portrait Safari brower (no extra files) */
@media only screen and (max-width: 810px) and (min-width: 810px) and (max-height: 977px) and (min-height: 977px)  { .potionsView {overflow: hidden; height: 94vh;}}

/* Exact metric for iPad 10.2"-inch portrait Safari brower (with extra files) */
@media only screen and (max-width: 810px) and (min-width: 810px) and (max-height: 1010px) and (min-height: 1010px)  { .potionsView {overflow: hidden; height: 97vh;}}

/* Exact metric for iPad Pro 12.9"-inch portrait */
/* Safari brower (with extra tabs) */ @media only screen and (max-width: 768px) and (min-width: 768px) and (max-height: 898px) and (min-height: 898px)  { .potionsView {overflow: hidden; height: 90vh;}}
